export const randomEmoji = () => {
    const placeholder = ["","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","",""];

    return placeholder[Math.floor(Math.random() * (placeholder.length - 1))];
  };

  export const randomTitle = () => {
    const titles = ["Wicked", "Cute", "Funky", "Snappy", "Bloody"];
    return titles[Math.floor(Math.random() * (titles.length - 1))];
  };
